import http from '../../common/getList.js';
export default {
	props: {
		imgurl: {}
	},
	data() {
		return {
			page: 0,
			cpage: 1,
			searchVal:'',
			searchList:[]
		};
	},
	mounted() {
		// this.getList()
	},
	methods: {
		getList(e) {
			var that = this;
			let url = 'api/home/index/videoList';
			let data = {
				page: that.page
			};
			http.request(url, data).then(
				res => {
					console.log(res);
					if (res.code === 1) {
						that.searchList = res.data.list;
					}
				},
				error => {}
			);
		},
		search1(e){
			console.log("asfsaf");
			var that = this;
			let url = 'api/home/index/videoList';
			let data = {
				keywords: that.searchVal
			};
			http.request(url, data).then(
				res => {
					console.log(res);
					if (res.code === 1) {
						that.imgurl = res.data.list;
					}
				},
				error => {}
			);
		},
		privewImg(e) {
			console.log(e.currentTarget.dataset.index);
			var that = this;
			let index = e.currentTarget.dataset.index;
			let imgobj = [];
			imgobj.push(that.imgurl[index].pic_url1);
			imgobj.push(that.imgurl[index].pic_url2);
			console.log(imgobj);
			uni.previewImage({
				urls: imgobj
			});
		}
	},

	components: {}
};
