import jianjie from '../../components/jianjie/jianjie.vue';
import llsignUp from '../../components/liaoyuanSignup/llsignUp.vue'
import playershow from '../../components/playershow/playershow.vue';
import signUp from '../../components/signUp/signUp.vue';
import http from '../../common/getList.js';
import uniNoticeBar from '../../components/uni-notice-bar/uni-notice-bar.vue';
import sempNoticeBar from '@/components/semp-notice-bar/semp-notice-bar.vue';
export default {
	data() {
		return {
			isshow: 1,
			banner: [],
			description: '',
			c_video: '',
			page: 0,
			pages: 0,
			title: '',
			videoList: [],
			currentPlay1: 0,
			currentPlay2: 0,
			index: 10,
			list: [
				'各位小朋友提交报名后，组委会将于每天18点进行审核通过',
				'各位小朋友提交报名后，组委会将于每天18点进行审核通过',
				'各位小朋友提交报名后，组委会将于每天18点进行审核通过'
			],
			imgurl: [] //图片数组
		};
	},
	onLoad(options) {
		
		this.getList();
	},

	components: {
		jianjie,
		llsignUp,
		playershow,
		signUp,
		uniNoticeBar,

		sempNoticeBar
	},
	methods: {
		toLiaoyuan(){
			this.isshow=4
		},
		toAgam(){
			window.Location.href="wx.agampai.com"
		},
		show(index) {
			this.isshow = index;
			if (this.isshow === 3) {
				this.getVideo();
			}
		},
		tiaozhuan(url){
			if(url&&url!=null){
				parent.location.href=url;
			}
		},
		getList(e) {
			let url = 'api/home/index/index';
			let data = {};
			http.request(url, data).then(
				res => {
					if (res.code === 1) {

						this.description = res.data.description;
						this.banner = res.data.banner;
						this.c_video = res.data.video.video;
						this.title = res.data.video.title;
						uni.hideLoading();
					}
				},
				error => {
					uni.hideLoading();
				}
			);
		},
		getVideo(e) {
			var that = this;
			let url = 'api/home/index/videoList';
			let data = {
				page: that.page
			};
			http.request(url, data).then(
				res => {
					that.pages = res.data.totalpage;
					for (var i = 0; i < res.data.list.length; i++) {
						that.imgurl.push(res.data.list[i]);
					}
				},
				error => {}
			);
		},
		playvideo1(e) {
			this.currentPlay1 = this.index;
		},
		playvideo2(e) {
			this.currentPlay2 = this.index;
		},
		playend1(e) {
			this.currentPlay1 = 0;
		},
		playend2(e) {
			this.currentPlay2 = 0;
		}
	},

	onReachBottom(e) {
		var that = this;
		if (that.isshow === 3) {
			if (that.page < that.pages) {
				that.page++;
				that.getVideo();
			} else {
			}
		}
	}
};
